import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useState } from 'react';
import './Header.scss';

function Header() {

    const [mobileMenu, setMobileMenu] = useState(false);
        
    return (
        <header className="header" id="Top">
            <a href="https://wa.me/79262059987" className="tel">
                <FontAwesomeIcon icon={['fab', 'whatsapp']} size="2x" /> 
                <span>+7 (926) 205-99-87</span>
            </a>
            <nav className="header-menu">
                <a className="header-menu__item" href="#AboutUs">О нас</a>
                <a className="header-menu__item" href="#Services">Наши услуги</a>
                <a className="header-menu__item" href="#Contacts">Где нас найти</a>
            </nav>
            <a href="https://www.instagram.com/brow_go_crocus/" className="socials">
                <span>Наш instagram:</span>     
                <FontAwesomeIcon icon={['fab', 'instagram']} size="2x" />
            </a>
            <nav className="mobile-header">
                <div className="brands">
                    <a href="https://wa.me/79262059987">
                        <FontAwesomeIcon icon={['fab', 'whatsapp']} size="2x" />
                    </a>
                    <a href="https://www.instagram.com/brow_go_crocus/"> 
                        <FontAwesomeIcon icon={['fab', 'instagram']} size="2x" />
                    </a>
                </div>
                <FontAwesomeIcon icon={['fas', 'bars']} size="2x" onClick={ () => setMobileMenu(true) } /> 
                <div className={ `mobile-menu ${ mobileMenu ? "visible-menu" : "hidden-menu" }` }>
                    <FontAwesomeIcon icon={['fas', 'times']} size="2x" onClick={ () => setMobileMenu(false) } />
                    <a href="#AboutUs" className="mobile-menu__item"
                    onClick={ () => setMobileMenu(false) }>О нас</a>
                    <a href="#Services" className="mobile-menu__item"
                    onClick={ () => setMobileMenu(false) }>Меню услуг</a>
                    <a href="#Contacts" className="mobile-menu__item"
                    onClick={ () => setMobileMenu(false) }>Где нас найти</a>
                    <div className="mobile-menu-subscribe">
                        <a className="mobile-menu-subscribe-btn" href="https://n447849.yclients.com/company:84576">
                            <i>Онлайн запись</i>            
                        </a>
                        <div className="ping"></div> 
                    </div>
                </div>
            </nav>
        </header>
    )
}

export default Header
